<template>
    <div class="center-selection">
        <el-input v-model="input" placeholder="请输入内容"></el-input>
        <div class="button">
            <h3>查询</h3>
        </div>
        <div class="echarts"></div>
    </div>
</template>

<script>
    export default {
        name: "index",
        data(){
            return{
                input:''
            }
        },
        methods:{
          createEcharts(){
              const myEchart=this.$echarts.init(document.querySelector('.echarts'))
              const option = {
                  title: {
                      text: 'Stacked Line'
                  },
                  tooltip: {
                      trigger: 'axis'
                  },
                  legend: {
                      data: ['Email', 'Union Ads', 'Video Ads', 'Direct', 'Search Engine'],
                      textStyle:{
                          color:'#fff'
                      }
                  },
                  grid: {
                      left: '3%',
                      right: '4%',
                      bottom: '3%',
                      containLabel: true
                  },
                  toolbox: {
                      feature: {
                          saveAsImage: {}
                      }
                  },
                  xAxis: {
                      type: 'category',
                      boundaryGap: false,
                        axisLabel:{
                          color: '#fff'
                        },
                      data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
                  },
                  yAxis: {
                      type: 'value',
                      axisLabel:{
                          color: '#fff'
                      },
                  },
                  series: [
                      {
                          name: 'Email',
                          type: 'line',
                          stack: 'Total',
                          data: [120, 132, 101, 134, 90, 230, 210]
                      },
                      {
                          name: 'Union Ads',
                          type: 'line',
                          stack: 'Total',
                          data: [220, 182, 191, 234, 290, 330, 310]
                      },
                      {
                          name: 'Video Ads',
                          type: 'line',
                          stack: 'Total',
                          data: [150, 232, 201, 154, 190, 330, 410]
                      },
                      {
                          name: 'Direct',
                          type: 'line',
                          stack: 'Total',
                          data: [320, 332, 301, 334, 390, 330, 320]
                      },
                      {
                          name: 'Search Engine',
                          type: 'line',
                          stack: 'Total',
                          data: [820, 932, 901, 934, 1290, 1330, 1320]
                      }
                  ]
              };
              myEchart.setOption(option)
          }
        },
        mounted() {
            this.createEcharts()
        }
    }
</script>

<style scoped>
    .center-selection{
        height: 367px;
        /*background-color: aqua;*/
        background-image: url("../../../../public/images/矢量智能对象 2.png"),url("../../../../public/images/line(1).png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        position: relative;
    }
    .el-input{

        position: absolute;
        top: 17%;
        left: 5%;

    }
    /deep/ .el-input__inner{
        background-color: rgba(13,93,155,1);
        border: 1px solid rgba(74,144,226,1);
        height: 40px;
        width: 242px;
    }
    /deep/ .el-input__inner:focus{
        background-color: white;
        box-shadow: 0 0 10px rgba(77,191,227,1);
    }
    .button{
        background-image: url("../../../../public/images/矢量智能对象.png");
        background-repeat: no-repeat;
        height: 41px;
        width: 96px;
        position: absolute;
        top: 17%;
        left: 35%;

    }
    .button:active{
        top: 17.2%;
        left: 35.2%;
    }
    h3 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-55%);
        color: white;
        font-weight: bold ;
    }
    .echarts{
        position: absolute;
        bottom: 5%;
        width: 90%;
        height: 60%;
        left: 50%;
        transform: translateX(-50%);

    }
</style>