<template>
    <div class="right-bottom">
        <div id="echarts"></div>
    </div>
</template>

<script>
    export default {
        name: "index",
        methods:{
            createMyChart() {
                const myChart=this.$echarts.init(document.getElementById('echarts'))
                const option={
                    title:{
                        text:'当日硫化氢数据统计图\n',
                        textStyle:{
                            color:'#fff'
                        },
                        left:50,
                        top:15
                    },
                    legend:{
                        orient:'vertical',
                        left: 60,
                        top:'35%',
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    series: [
                        {
                            name: 'Access From',
                            type: 'pie',
                            radius: '70%',
                            center:['60%','55%'],
                            labelLine:{
                                show:false
                            },
                            label:{
                                show:false
                            },
                            data: [
                                {value: 1048, name: 'Search Engine'},
                                {value: 735, name: 'Direct'},
                                {value: 580, name: 'Email'},
                                {value: 484, name: 'Union Ads'},
                                {value: 300, name: 'Video Ads'}
                            ],
                        }
                    ]
                }
                myChart.setOption(option)
            }
        },
        mounted() {
            this.createMyChart()
        }
    }
</script>

