<template>
    <div class="right-top">
        <div id="main"></div>
    </div>
</template>

<script>
    export default {
        name: "index",
        methods:{
           creatMyChart(){
               const myChart=this.$echarts.init(document.getElementById('main'))
               const option = {
                    title:{
                        text:"当日异常和危险数据统计图\n",
                        textStyle: {
                            color: '#fff',
                        },
                        left:50,
                        top:15
                    },
                   legend: {
                       // top: 'bottom',
                       bottom:13,
                       textStyle: {
                           color:'#fff'
                       }
                   },
                   series: [
                       {
                           name: 'Nightingale Chart',
                           type: 'pie',
                           radius: ["20%", "60%"],
                           center: ['50%', '50%'],
                           itemStyle: {
                               borderRadius: 8
                           },
                            label:{
                               textBorderWidth:0
                            },
                           data: [
                               { value: 40, name: 'rose 1' },
                               { value: 38, name: 'rose 2' },
                               { value: 32, name: 'rose 3' },
                               { value: 30, name: 'rose 4' },
                               { value: 28, name: 'rose 5' },

                           ]
                       }
